//global vars
$transition-time: 0.4s;
$max-mobile-width: 768px;
$main-background-color: rgb(247, 247, 211);
$main-background-color-lighter: rgb(255, 255, 240);
$main-background-color-darker: rgb(247, 247, 184);
body {
  background-color: $main-background-color;
}
p {
  margin: 0;
  cursor: default;
}
ul {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
